<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col>
          <el-form ref="formRef" :model="queryVo" label-width="80px" inline>
            <el-form-item label="" prop="stuName">
              <el-input
                v-model="queryVo.stuName"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="licName">
              <el-input
                v-model="queryVo.licName"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="licType">
              <el-input
                v-model="queryVo.licType"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
             <el-form-item label="" prop="licLevel">
              <el-input
                v-model="queryVo.licLevel"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="licGetTime">
              <el-input
                v-model="queryVo.licGetTime"
                @keyup.enter.native="queryPage"
                placeholder=""
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                ></el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="open = true"
            ></el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="editUser"
            v-if="is_authority.is_user_upt"
            ></el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            ></el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            ></el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            ></el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!--  -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          type="index"
          width="80"
          align="center"
          label=""
        ></el-table-column>

        <el-table-column
          prop="stuName"
          align="center"
          label=""
        ></el-table-column>

        <el-table-column
          prop="licName"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licType"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licNo"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licLevel"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licGetTime"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licPeriodLimit"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licInstitution"
          label=""
          align="center"
        ></el-table-column>

        <el-table-column
          prop="licPhoto"
          label=""
          align="center"
        >
        <template slot-scope="scope">
              <img
                @click="photo(scope.row)"
                :src="scope.row.licPhoto"
                style="width: 50px;height: 50px"
              >
          </template>
        </el-table-column>

        <el-table-column
          prop="licDemo"
          label=""
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!--  :before-close="handleClose" -->
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <!--  -->

        <el-form
          :model="Form"
          :rules="rules"
          ref="formRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="stuId">
                <el-select
                  v-model="Form.stuId"
                  filterable
                  placeholder=""
                >
                  <el-option
                    v-for="item in stuOptions"
                    :key="item.stuId"
                    :label="item.stuName"
                    :value="item.stuId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="licName">
                <el-input v-model="Form.licName"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="licNo">
                <el-input v-model="Form.licNo"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="licGetTime">
                <el-input v-model="Form.licGetTime"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

            
          

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="licInstitution">
                <el-input v-model="Form.licInstitution"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item
                label-width="100px"
                label=""
                prop="licPhoto"
              >
                <el-upload
                  :file-list="fileList"
                  class="upload-demo"
                  action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
                  multiple
                  :headers="config"
                  :on-success="saveUrl"
                  :before-upload="beforeUpload"
                  accept=".jsp,.png"
                >
                  <el-button icon="el-icon-upload2" size="small" type="primary"
                    ></el-button
                  >
                  <span slot="tip" class="el-uploaddTip">jpg/png</span>
                </el-upload>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="" prop="licType">
                <el-input v-model="Form.licType"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="licPeriodLimit">
            <el-input v-model="Form.licPeriodLimit"></el-input>
          </el-form-item>
            </el-col>
          </el-row>

            <el-row :gutter="10">
                <el-col :span="12">
                    <el-form-item label="" prop="licLevel">
                <el-input v-model="Form.licLevel"></el-input>
              </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="licDemo">
                <el-input v-model="Form.licDemo"></el-input>
              </el-form-item>
                </el-col>
            </el-row>
          
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="open = false"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </span>
      </el-dialog>
    </el-card>

    <el-dialog
          :visible.sync="dialogVisible">
          
          <img width="100%" :src="photoView" >

        </el-dialog>

        
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      token: window.sessionStorage.getItem('token'),
      photoView:'',
      dialogVisible: false,
      fileList: [],
      stuOptions: [],
      Form: {},
      ids: [],
      open: false,
      // 
      single: true,
      // 
      multiple: true,
      List: [],
      queryVo: {
        stuName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      },
      rules: {
        licInstitution: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licPhoto: [
          { required: true, message: '', trigger: 'blur' }
        ],
        stuId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licType: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licNo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licGetTime: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licDemo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licLevel: [
          { required: true, message: '', trigger: 'blur' }
        ],
        licPeriodLimit: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
    this.getTreeselect()
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    photo(row){
      console.log(row);
      this.dialogVisible = true
      this.photoView=row.licPhoto
    },
    beforeUpload(file){
        console.log(file.type);

        const isjpg = file.type === 'image/png' ||file.type === 'image/jpeg';
        if (!isjpg) {
          this.$message.error(' JPGPNG !');
        }
        return isjpg
    },
    async saveUrl(response, file, fileList) {
      this.Form.licPhoto = response.data.url
      console.log(response);
    },
    async getTreeselect() {
      const { data: res } = await this.$http.post(
        '/studentInfo/queryStudentInfoList',
        {
          page: {
            current: 1,
            size: 999
          }
        }
      )
      this.stuOptions = res.data.dataList.records
      if (res.code != 20000) return this.$message.error(res.message)
    //   console.log(this.stuOptions)
      // console.log(this.majorOptions)
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },
    submitForm() {
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            '/stuLicenseInfo/saveOrUpdateStuLicenseInfo',
            this.Form
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.open = false
          this.queryPage()
          return this.$message.success(res.message)
        }
      })
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    async editUser(row) {
      this.open = true
      const id = row.id || this.ids
      console.log(id)
      const { data: res } = await this.$http.get(
        '/stuLicenseInfo/getByIdStuLicenseInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      console.log(res)
      this.Form = res.data.entity
    },

    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      console.log(row);
      const userid = a || this.ids
      this.$confirm(
        '?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      )
        .then(async () => {
          const { data: res } = await this.$http.delete(
            '/stuLicenseInfo/removeByIdStuLicenseInfos',
            {
              data : userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          })
        })
    },
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/stuLicenseInfo/queryStuLicenseInfoList',
        this.queryVo
      )
      console.log(res.data.dataList.records)
      if (res.code != 20000) return this.$message.error(res.message)
      this.List = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
      // console.log(this.List)
    },
    //
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    // 
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    },
    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
